<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}">
      <div style="padding-top: 1rem;">
        <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;   ">
          <!-- <nav-head :title="title" ></nav-head> -->
          <nav-head :title="languageStatus =='zh' ? '订单历史':'Order history'" ></nav-head>
        </div>
        <!-- tab 栏 -->
        <div class="tab-box">
            <van-tabs @click="navClick" v-model="active" swipe-threshold=6 color="#06A9FF" background="#191E35" title-active-color="rgba(255,255,255,0.7)" title-inactive-color="rgba(255,255,255,0.5)" line-height="0.02rem">
              <van-tab :title="v.title" v-for="(v,i) in navList" :key="i">
                <!-- 排序   -->
                <div class="paixu"><div><span>{{$t('common.leixing_shuliang')}}</span></div><span>{{$t('common.zonge_zhuangtai')}}</span></div>
                <!--列表内容-->
                <div class="list-item" v-for="(item, index) in recordList" :key="index">
                    <div class="row-1">
                        <div class="row-1-left">
                            <span v-if="languageStatus=='en'" class="green-font">{{item.type == 1?'Sell':'Buy'}}</span>
                            <span v-else class="green-font">{{item.type == 1?'卖出':'买入'}}</span>
                        <span class="name-b">{{item.num}} ETC</span>
                        </div>
                        <span class="green-font">￥ {{item.amount}}</span>
                    </div>
                    <div class="row-2">
                        <span class="time-a">{{item.ctime}}</span>
                        <span v-if="languageStatus=='en'" class="status">{{item.status == 1?'Successful':item.status == 2? 'cancel':'No pay'}}</span>
                        <span v-else class="status">{{item.status == 1?'交易成功':item.status == 2? '取消':'待付款'}}</span>
                    </div>
                </div>
              </van-tab>
            </van-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { record } from '@/api/user'
import NavHead from '@/components/head.vue'
import { objKeySort, sgin } from '@/utils/auth'
import { Tab, Tabs, Toast } from 'vant'
Vue.use(Tab).use(Tabs).use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      languageStatus: '',
      title: '订单历史',
      navList: [
        { title: '全部', type: 1 },
        { title: '买入', type: 2 },
        { title: '卖出', type: 3 },
        { title: '待付款', type: 4 },
        { title: '取消', type: 5 }
      ],
      curHeight: '',
      activeBtn: 1,
      active: 0,
      recordData: {
        u_id: '',
        status: '1',
        page: '1',
        showpage: '50'
      },
      recordList: []
    }
  },
  created () {
    this.recordData.u_id = sessionStorage.getItem('u_id')
    this.sellCoinFun()
    this.beforeMount()
    this.getLanguageStatus()
    this.gg()
  },
  methods: {
    gg () {
      if (this.languageStatus === 'en') {
        this.navList = [
          { title: 'All', type: 1 },
          { title: 'Buy', type: 2 },
          { title: 'Sell', type: 3 },
          { title: 'No pay', type: 4 },
          { title: 'Cancel', type: 5 }
        ]
      }
      if (this.languageStatus === 'zh') {
        this.navList = [
          { title: '全部', type: 1 },
          { title: '买入', type: 2 },
          { title: '卖出', type: 3 },
          { title: '待付款', type: 4 },
          { title: '取消', type: 5 }
        ]
      }
    },
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    navClick (index) {
      var that = this
      console.log(index, 'index', that)
      that.recordData.status = that.navList[index].type
      console.log(index, 'index', this.recordData)
      this.sellCoinFun()
    },
    // 点击切换按钮1
    changBtn1 () {
      this.activeBtn = 1
    },
    // 点击切换按钮2
    changBtn2 () {
      this.activeBtn = 2
    },
    sellCoinFun () {
      var that = this
      var data = objKeySort(that.recordData)
      var token = sgin(data)
      this.recordData.token = token
      record(that.recordData).then(res => {
        console.log(res, that, '法币订单详情')
        if (res.code === 0) {
          // that.$toast({ message: res.data.msg, duration: 1500 })
          that.recordList = res.data.list
          delete that.recordData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        // that.recordData.phone = ''
        delete that.recordData.token
      })
    }
  }
}
</script>
